  background: linear-gradient(#000 0%, #000000e6 75%, #000c 100%);
  padding-top: 10rem;
}

.about-section p {
  margin-bottom: 5rem;
}

.projects-section {
  padding: 10rem 0;
}

.projects-section .featured-text {
  padding: 2rem;
}

@media (min-width: 992px) {
  .projects-section .featured-text {
    border-left: .5rem solid #64a19d;
    padding: 0 0 0 2rem;
  }
}

.projects-section .project-text {
  padding: 3rem;
  font-size: 90%;
}

@media (min-width: 992px) {
  .projects-section .project-text {
    padding: 5rem;
  }

